import { useState, useEffect } from 'react';
import { useMedia } from 'react-use';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { Navbar, Nav } from 'react-bootstrap';
import { AiOutlineMenu, AiOutlineClose } from 'react-icons/ai';
import ConnectWalletModal from '../Modals/ConnectWalletModal';
import AlertModal from '../Modals/AlertModal';
import { useWalletConnect } from '../../hooks/WalletConnect';

const NavWrapper = styled(Navbar)`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  color: #fff;
  padding: 40px 0;

  @media screen and (max-width: 991px) {
    .navbar-nav {
      padding: 0 54px;
      align-items: start !important;
      margin-left: 0 !important;
      gap: inherit !important;
      a {
        margin-bottom: 24px;
        font-size: 24px;
        font-weight: 700;
      }
    }
    #basic-navbar-nav {
      &.show {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: start;
      }
    }
    .terms-privacy {
      font-size: 24px;
      font-weight: 700;
    }
  }

  a {
    color: #fff;
    text-transform: uppercase;
    font-size: 21px;
    font-weight: bold;
    white-space: nowrap;
    @media (max-width: 1440px) and (min-width: 1100px) {
      font-size: 17px;
    }
    @media (max-width: 1100px) and (min-width: 992px) {
      font-size: 15px;
    }
  }
`;

const ConnectButton = styled.button`
  background: #8f8f8f;
  border: 3px solid #e2ae39;
  border-radius: 32.5px;
  font-size: 21px;
  padding: 0 20px;
  color: white;
  font-weight: bold;
  line-height: 65px;
  white-space: nowrap;
  @media (max-width: 1440px) and (min-width: 1100px) {
    line-height: 46px;
    font-size: 17px;
  }
  @media (max-width: 1100px) and (min-width: 992px) {
    font-size: 15px;
    line-height: 46px;
  }
  @media screen and (max-width: 991px) {
    line-height: 54px;
  }
`;

const Container = styled.div`
  width: 100%;
  padding: 0 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media (max-width: 1440px) and (min-width: 992px) {
    padding: 0 15px;
  }

  @media screen and (max-width: 991px) {
    padding: 0;
  }
`;

const NavbarCollapse = styled(Navbar.Collapse)`
  @media (max-width: 1440px) and (min-width: 1024px) {
    .navbar-nav {
      gap: 1rem !important;
    }
  }
  @media (max-width: 1024px) and (min-width: 991px) {
    .navbar-nav {
      gap: 0.7rem !important;
    }
  }
  @media screen and (max-width: 991px) {
    position: fixed;
    top: 150px;
    left: 0;
    bottom: 0;
    width: 100vw;
    background: #000000;
  }
`;

const BrandImage = styled.img`
  height: 84px;

  @media (max-width: 1440px) and (min-width: 992px) {
    height: 67px;
  }
  @media screen and (max-width: 991px) {
    height: 50px;
    padding-left: 22px;
  }
`;

const MyWalletLink = styled(Link)`
  background: #e2ae39;
  color: #000;
  display: block;
  border-radius: 32.5px;
  font-size: 21px;
  padding: 0 20px;
  color: white;
  font-weight: bold;
  line-height: 65px;
  white-space: nowrap;
  &:hover {
    color: #ffffff;
  }
  @media (max-width: 1440px) and (min-width: 991px) {
    line-height: 46px;
  }
  @media screen and (max-width: 991px) {
    line-height: 54px;
  }
`;

const Navigation = () => {
  const isMobile = useMedia('(max-width: 991px)');
  const navigate = useNavigate();
  const { myWalletAddress } = useWalletConnect();
  const [openConnectModal, setOpenConnectModal] = useState(false);
  const [menuOpened, setMenuOpened] = useState(false);
  const [openSuccessModal, setOpenSuccessModal] = useState(false);
  const [alertMessage, setAlertMessage] = useState('');
  const [currentPathName, setCurrentPathName] = useState('');

  const handleCloseModal = (connected?: boolean) => {
    setOpenConnectModal(false);
    if (connected !== undefined) {
      setAlertMessage(
        connected ? 'Your wallet is now connected' : 'Your wallet connection was unsuccessful'
      );
      setOpenSuccessModal(true);
    }
  };

  const handleOpenModal = () => {
    if (myWalletAddress) {
      navigate('/my-wallet');
    } else {
      setOpenConnectModal(true);
    }
  };

  useEffect(() => {
    if (isMobile) {
      setMenuOpened(false);
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      document.querySelector('#basic-navbar-nav').style.display = 'none';
      if (!document.querySelector('.navbar-toggler')?.classList.contains('collapsed')) {
        document.querySelector('.navbar-toggler')?.classList.add('collapsed');
      }
    }
  }, [location.pathname]);

  useEffect(() => {
    if (menuOpened) {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      document.querySelector('#basic-navbar-nav').style.display = 'flex';
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      document.querySelector('#basic-navbar-nav').style.justifyContent = 'space-between';
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      document.querySelector('#basic-navbar-nav').style.alignItems = 'start';
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      document.querySelector('#basic-navbar-nav').style.flexDirection = 'column';
    } else {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      document.querySelector('#basic-navbar-nav').style.display = 'none';
    }
  }, [menuOpened]);

  useEffect(() => {
    if (menuOpened) {
      (document.querySelector('.page') as HTMLBaseElement).style.display = 'none';
    } else {
      (document.querySelector('.page') as HTMLBaseElement).style.display = 'block';
    }
  }, [menuOpened, navigate]);

  return (
    <NavWrapper expand="lg">
      <Container>
        <Link className="navbar-brand" to="/">
          <BrandImage src="/logo.svg" alt="Md Token Logo" height="84px" />
        </Link>
        <Navbar.Toggle
          aria-controls="basic-navbar-nav"
          onClick={() => setMenuOpened((value) => !value)}
        >
          {menuOpened ? (
            <AiOutlineClose size={36} color="#ffffff" />
          ) : (
            <AiOutlineMenu size={36} color="#ffffff" />
          )}
        </Navbar.Toggle>
        <NavbarCollapse id="basic-navbar-nav">
          <Nav className="ms-auto d-flex align-items-center gap-4">
            <Link to="/about">About</Link>
            <Link to="/accumulate">Accumulate</Link>
            <Link to="/collect-rewards">Collect Rewards</Link>
            <Link to="/governance">Governance</Link>
            <Link to="/proposals">Proposals</Link>
            {myWalletAddress ? (
              <MyWalletLink to="/my-wallet">MY WALLET</MyWalletLink>
            ) : (
              <ConnectButton onClick={handleOpenModal}>CONNECT YOUR WALLET</ConnectButton>
            )}
          </Nav>
          {isMobile ? (
            <Nav className="flex-row terms-privacy">
              <Link to="/terms" className="me-2">
                TERMS
              </Link>
              {' & '}
              <Link to="/privacy" className="ms-2">
                PRIVACY
              </Link>
            </Nav>
          ) : (
            ''
          )}
        </NavbarCollapse>
      </Container>
      {openConnectModal && (
        <ConnectWalletModal show={openConnectModal} onClose={handleCloseModal} />
      )}
      {openSuccessModal && (
        <AlertModal
          show={openSuccessModal}
          onClose={() => setOpenSuccessModal(false)}
          message={alertMessage}
        />
      )}
    </NavWrapper>
  );
};

export default Navigation;
